.card {
  height: 250px;
  position: relative;
  width: 250px;
  border: 2px solid black;
  border-radius: 50px;
}

.card>.img-container {
  text-align: center;
}

.card>.img-container>img {
  width: 60%
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: #2A77AE;
  margin-bottom: 50px;
}

.clicky{
    background-color: aqua;
    width: 100%;
}
h1{
    float: left;
    text-align: center;
    
}
p{
    font-size: 30px;
    float: right;
}

